import 'isomorphic-fetch';

import {
    JOIN_REQUEST,
    JOIN_RECEIVE,
} from '../constants/join';

export const joinRequest = n => ({
    type: JOIN_REQUEST,
    amount: n
});

export const joinReceive = (n, stories) => ({
    type: JOIN_RECEIVE,
    amount: n,
    data: stories
});

export const joinPost = n => (dispatch) => {
    dispatch(joinRequest(n));
    return fetch('http://0.0.0.0:9527/api/join/')
    .then((response) => {
        if (response.status >= 400) {
            console.log('net work fail');
        }
        return response.json();
    })
    .then((json) => {
        dispatch(joinReceive(n, json.data));
    }).catch(ex =>
        console.log('parsing failed', ex)
    );
};



